@import url(https://fonts.google.com/specimen/Baloo+Chettan+2);
.hero-card {
border: 8px solid black;
width:250px;
display:flex;
flex-direction: column;
justify-content: center;
align-items: center;
background: rgb(65,64,63);
background: linear-gradient(90deg, rgba(65,64,63,1) 0%, rgba(41,126,145,1) 100%);
}

.hero-list {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(4, 1fr);
    margin: auto;
    align-items: center;
    justify-items: center;
    grid-gap:5px;
    gap:5px;
}

.hero-title {
    font-style: bold;
}



.search{
    position:absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    top:110px;
    height: 30px;
}

 .details-button {
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    background: #60a3bc;
    padding: 10px;
    border-radius: 50px;
    border: none;
    transition: all 0.4s ease 0s;
    font-weight: 900;
    margin-bottom: 20px;
 }

 .details-button:hover{
        background:#156481;
        color:white;
 }

 @media (max-width:1100px) {
     .hero-list {
        grid-template-columns: repeat(3, 1fr);
     }
 }

 @media (max-width:800px) {
    .hero-list {
        grid-template-columns: repeat(2, 1fr);
     }
 }

 @media (max-width:540px) {
    .hero-list {
        grid-template-columns: repeat(1, 1fr);
     }
  
 }
.detail-popup {
    position: fixed;
    top:0;
    left:10%;
    width:70%;
    height:80%;
    background-color: black;
    margin-top: 2%;
    padding:5%;
    padding-top: 2%;
    overflow-y: scroll;
    color:white;
    display:block;
    z-index: 3;

}

hr{
    width:100%;
}
.detail-card{
    display:flex;
    flex-direction: column;
    width:100%;
    
}


.detail-rows {
   display:grid;
   grid-auto-flow: row;
    grid-gap:4%;
    gap:4%;
    overflow-wrap: break-word;
    grid-template-columns: repeat(2, 1fr);
}

.detail-rows div {
    font-size: 1.5em;
    padding:1em;
    min-width: 30%;
    border: 2px solid black;
}

.closer{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: ' ';
    background: rgba(0,0,0,.5);
  
}

img{
    size:contain;
    border-radius: 50%;
}

.img-container{
    margin:auto;
}

@media (max-width:700px) {
  .detail-popup{
      width:90%;
      left:5%;
      padding:0;
     
  }

  .detail-rows div {
      padding: 0.3em;
    
  }
  
 }
html{
    background: rgb(166,166,166);
    background: linear-gradient(90deg, rgba(166,166,166,1) 0%, rgba(120,116,100,1) 100%);
}


.App{
width:100%;
height:auto;
}

h1 {
    margin:40px;
    margin-bottom: 60px;
    text-align: center;
    font-size: 3em;
    color:white;
    font-family: 'Baloo Chettan 2', cursive;
}

@media (max-width:540px) {
    h1{
        margin:5px;
        margin-bottom: 100px;
     }
  
 }
